// -------------------------------------------------------------------------
//		Header
//--------------------------------------------------------------------------
.header {
	flex: 1 0 auto;
	
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	margin: 0 0 3vh;
	padding: 1vh 0;

	.logo {

		h3 {
			position: relative;
			// font-family: sans-serif;
			// text-transform: uppercase;
			// font-size: 1rem;
			// letter-spacing: 4px;

			&:before {
				content: url(./../../images/logo.png);
				display: inline-block;
				vertical-align: middle;
				font-style: normal;
			}
		}
		p {
			opacity: 0.6;
			font-size: calc(4px + 2vmin);
			text-align: end;
		}
		
	}
}