.progress-circle {
  position: relative;
  margin: 5%;
  padding: 0;
  width: 5em;
  height: 5em;
  border-radius: 50%;
  background: linear-gradient(45deg, #05afe5 0%, #05e2b3 100%);
  -webkit-text-fill-color: black;
  font-size: 1rem;
  line-height: 5em; }
  .progress-circle::after {
    display: block;
    position: absolute;
    top: 0.35em;
    left: 0.35em;
    border: none;
    border-radius: 50%;
