.toggle {
  display: block;
  position: absolute;
  top: 1vh;
  right: calc(1.5vw + 6px); }
  .toggle input[type=checkbox] {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    max-width: 94px;
    height: 30px;
    cursor: pointer;
    appearance: none; }
    .toggle input[type=checkbox]::before {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(-50%, -50%);
      content: '\0263D'; }
    .toggle input[type=checkbox]:checked::before {
      color: transparent; }
    .toggle input[type=checkbox]:checked::after {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(50%, -50%);
      color: var(--white);
      content: '\02600'; }
    .toggle input[type=checkbox]:checked ~ span::before {
      background-color: var(--blue); }
    .toggle input[type=checkbox]:checked ~ span::after {
      top: 0;
      left: 65px; }
    .toggle input[type=checkbox]:hover ~ span::before {
      background-color: #3db5eb; }
    .toggle input[type=checkbox]:not(:checked):hover ~ span::before {
      background-color: #afbec9; }
    .toggle input[type=checkbox]:focus ~ span::before {
      outline: none;
      box-shadow: 0 0 0 4px rgba(21, 156, 228, 0.4); }
    .toggle input[type=checkbox]:checked ~ label::before {
      content: "Turn off the light"; }
    .toggle input[type=checkbox]:checked ~ .darkmode {
      opacity: 1; }
  .toggle label::before {
    content: 'Turn on the light';
    display: block;
    font-size: 0.8rem;
    font-weight: 100;
    line-height: 1.5;
    color: var(--white); }
  .toggle span {
    display: inline-block;
    position: relative;
    vertical-align: middle;
    cursor: pointer; }
    .toggle span::before {
      display: block;
      width: 94px;
      height: 29px;
      content: "";
      background-color: #8296a4;
      border-radius: 29px;
      transition: background-color 0.3s ease; }
    .toggle span::after {
      position: absolute;
      top: 0;
      left: 1px;
      display: block;
      width: 29px;
      height: 29px;
      visibility: visible;
      content: "";
      background-color: var(--white);
      border-radius: 29px;
      transition: left 0.3s ease; }
  .toggle .darkmode {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    transition: all .3s ease-in-out;
    mix-blend-mode: difference;
    background: var(--white); }

